    width: 35px;
    height: 30px;
  }
}

@media (min-width: 1200px) {
  .football-animation {
    width: 40px;
    height: 40px;
  }
}

@keyframes bounce {
  0% {
    transform: translate(0, -200%)rotate(-50deg);
  }

  20% {
    margin-left: 35px;
    transform: translate(0)rotate(50deg);
  }

  40% {
    transform: translate(0, -150%)rotate(50deg);
  }

  60% {
    margin-left: 35px;
    transform: translate(0);
  }

  80% {
    margin-left: 30px;
    transform: translate(0, -130%);
  }

  100% {
    margin-left: 25px;
    transform: translate(0)rotate(-50deg);
  }
}

